import React from "react";
import { useFormik } from "formik";
import { useDispatch } from "react-redux";
import { addExpense } from "../store/expensesSlice";
import { Form, Button } from 'react-bootstrap';
import * as Yup from 'yup';

//      Yup
const validationSchema = Yup.object({
    category: Yup.string().required(" "),
    amount: Yup.number()
        .required(" ")
        .positive("    ")
        .typeError("   "),
    date: Yup.date().required(" "),
    currency: Yup.string().required(" ")
});

const ExpenseForm: React.FC = () => {
    const dispatch = useDispatch();

    const formik = useFormik({
        initialValues: {
            category: '',
            amount: '',
            date: '',
            currency: 'BYN'
        },
        validationSchema, //    Yup
        onSubmit: (values) => {
            dispatch(addExpense({ 
                id: Date.now(), 
                amount: +values.amount, 
                category: values.category, 
                date: values.date, 
                currency: values.currency
            }));
            formik.resetForm();
        }
    });

    return (
        <Form onSubmit={formik.handleSubmit}>
            <Form.Group controlId="category">
                <Form.Label></Form.Label>
                <Form.Control
                    as="select"
                    name="category"
                    onChange={formik.handleChange}
                    value={formik.values.category}
                    isInvalid={!!formik.errors.category && formik.touched.category}
                >
                    <option value=""> </option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Form.Control>
                <Form.Control.Feedback type="invalid">{formik.errors.category}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="amount" className="mt-3">
                <Form.Label></Form.Label>
                <Form.Control
                    type="number"
                    name="amount"
                    onChange={formik.handleChange}
                    value={formik.values.amount}
                    placeholder=" "
                    isInvalid={!!formik.errors.amount && formik.touched.amount}
                />
                <Form.Control.Feedback type="invalid">{formik.errors.amount}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="currency" className="mt-3">
                <Form.Label></Form.Label>
                <Form.Control
                    as="select"
                    name="currency"
                    onChange={formik.handleChange}
                    value={formik.values.currency}
                    isInvalid={!!formik.errors.currency && formik.touched.currency}
                >
                    <option value="BYN">BYN (. .)</option>
                    <option value="USD">USD ()</option>
                    <option value="EUR">EUR ()</option>
                    <option value="RUB">RUB (. .)</option>
                </Form.Control>
                <Form.Control.Feedback type="invalid">{formik.errors.currency}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="date" className="mt-3">
                <Form.Label></Form.Label>
                <Form.Control
                    type="date"
                    name="date"
                    onChange={formik.handleChange}
                    value={formik.values.date}
                    isInvalid={!!formik.errors.date && formik.touched.date}
                />
                <Form.Control.Feedback type="invalid">{formik.errors.date}</Form.Control.Feedback>
            </Form.Group>

            <Button type="submit" variant="primary" className="mt-4 w-100">
                 
            </Button>
        </Form>
    );
}

export default ExpenseForm;
